.inner {
	position: relative;
	z-index: 10;
	padding: 0 105px;
	margin: 0 auto;
	max-width: 1366px;
	@include clr;
	@include md {
		padding: 0 50px; }
	@include sm {
		padding: 0 30px; } }

.pull-left {
	float: left; }
.pull-right {
	float: right; }

.clear {
	@include clr; }

.title-lg {
	@include f(rt,80,88); }

.title-sm {
	@include f(oeb,16,24,40);
	text-transform: uppercase; }
