.review {
	padding: 30px 10px;
	margin: 0 auto;
	max-width: 290px;
	@include md {
		max-width: 100%;
		padding: 30px 0; }
	&__img {
		margin-bottom: 20px;
		border-radius: 50%;
		width: 60px;
		height: 60px; }
	&__meta {
		color: #b9b9b9;
		@include f(rt,12,24,40); }
	&__text {
		padding-top: 20px;
		@include f(rt,14,32,40); } }
