.header {
	padding-bottom: 29px;
	&__btn-menu {
		float: left;
		margin-top: 29px;
		z-index: 12; }
	&__inner {
		z-index: initial; }
	&__logo {
		margin: 27px 0 0 25px;
		float: left; }
	&__menu {
		float: left;
		z-index: 11; }
	&__list {
		float: right;
		margin-top: 30px;
		@include f('oeb', 12);
		text-transform: uppercase;
		li {
			vertical-align: top;
			display: inline-block;
			&:not(:last-child) {
				margin-right: 10px; } }
		a {
			color: $black; } } }

.logo {
	img {
		display: block; } }

.menu {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	transform: translateY(-100%);
	background-color: $white;
	&__inner {
		position: relative;
		padding-top: 30px;
		padding-bottom: 168px; }
	&__logo {
		position: absolute;
		left: 15px;
		bottom: 50px; }
	&__wrap {
		padding-left: 25%;
		@include clr; }
	&__col {
		float: left;
		width: 33%;
		padding-left: 20px; }
	&__title {
		margin-bottom: 95px;
		@include f('rt', 12);
		color: $lightGrayText; }
	&__list {
		color: $black;
		text-transform: uppercase;
		@include f('ob', 18);
		&_followUs {
			color: $lightGrayText;
			@include f('ol');
			text-transform: initial; } }
	li {
		&:not(:last-child) {
			margin-bottom: 25px; } }
	a {
		color: inherit; } }
