.results {
	&__container {
		padding: 200px 0 163px;
		margin: 0 auto;
		max-width: 1000px;
		@include clr; }
	&__title {
		padding: 53px 0 7px;
		color: $black;
		background: $white;
		@include f(rb, 16, 60);
		text-align: center;
		text-transform: uppercase; }
	&__info {
		float: left;
		padding-top: 10px;
		max-width: 200px; }
	&__box {
		padding: 28px 0 27px;
		&:not(:last-child) {
			&:after {
				content: '';
				display: block;
				border-bottom: 1px solid #d1d1d1;
				max-width: 190px; } } }
	&__subtitle {
		@include f(ob, 24, 60);
		text-transform: uppercase; }
	&__content {
		@include f(rt, 14, 28, 40);
		margin-bottom: 68px; }
	&__images {
		position: relative;
		float: right;
		max-width: 610px;
		width: 100%;
		height: 480px; }
	&__image {
		position: absolute;
		width: 320px;
		height: 320px;
		background: #f8f8f8;
		&[data-item="1"] {
			z-index: 10;
			top: 0;
			left: 0; }
		&[data-item="2"] {
			z-index: 5;
			bottom: 0;
			right: 0; }
		&-in {
			width: 100%;
			height: 100%; } } }



